.bar {
    background-color: #00ff95;
    position: fixed;
    top: 4%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    z-index: 100;
    border-radius: 59px 59px 59px 59px;
    border: 0px solid #000000;
    box-shadow: 0px 0px 14px 0px rgba(0, 255, 149, 0.75);
    color: #020d09;
}